import axios from 'axios'
import { variable } from '../configs/variable'

export const getPaiements = async (id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const url  = id ? `${endpoint}/api/paiements/${id}` : `${endpoint}/api/paiements`

        const { data } = await axios.get(`${url}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const deletePaiement = async (id) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        
        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const { data } = await axios.delete(`${endpoint}/api/paiements/${id}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const addPaiement = async (paiement, id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        let reponse = null

        if (id) {
            reponse =  await axios.put(`${endpoint}/api/paiements/${id}`, paiement, config)
        } else {
            reponse =  await axios.post(`${endpoint}/api/paiements`, paiement, config)
        }

        return {status: true, data: reponse?.data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}