import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
  CButton,
  CButtonGroup,
} from '@coreui/react'
import { Authentification } from '../../components/index'

import { useHistory } from 'react-router-dom'

import Loader from '../../components/Loader'
import Message from '../../components/Message'

import { getPaiements, deletePaiement } from '../../provider/paiementFetch'

import moment from 'moment'

const Paiements = () => {
  /** State */
  const [paiements, setPaiements] = useState(null)
  const [error, setError] = useState(null)

  /** Hook Route */
  let history = useHistory()

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log('Run once, when the component mounts')

    async function fetch() {
      const resp = await getPaiements()

      resp?.status ? setPaiements([...resp?.data]) : setError(resp?.error)
    }

    fetch()
  }, [])

  const deleteFunction = async (id) => {
    const resp = await deletePaiement(id)

    if (resp.status) {
      setPaiements((old) => old.filter((u) => u._id !== id))
    }
  }

  const ajout = () => {
    history.push(`/ajout-paiement`)
  }

  const edit = (id) => {
    history.push(`/ajout-paiement/${id}`)
  }

  if (paiements === null) {
    return <Loader />
  }

  if (error) {
    return <Message> {error}</Message>
  }

  return (
    <CRow>
      <Authentification />
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Listes des Paiements</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => ajout()}
                  >
                    Ajout
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CTable>
              <CTableHead>
                <CTableRow>
                  <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                  <CTableHeaderCell scope="col">iban</CTableHeaderCell>
                  <CTableHeaderCell scope="col">montant</CTableHeaderCell>
                  <CTableHeaderCell scope="col">date</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Actions</CTableHeaderCell>
                </CTableRow>
              </CTableHead>
              <CTableBody>
                {paiements.length > 0 ? (
                  paiements.map((paiement) => (
                    <CTableRow key={paiement._id}>
                      <CTableDataCell>{paiement.name}</CTableDataCell>
                      <CTableDataCell>{paiement.iban}</CTableDataCell>
                      <CTableDataCell>{paiement.montant}</CTableDataCell>
                      <CTableDataCell>
                        {moment(paiement.datePaiement).local().format(
                          'DD/MM/YYYY HH:mm'
                        )}
                      </CTableDataCell>
                      <CTableDataCell></CTableDataCell>
                      <CTableDataCell>
                        <CButton
                          color={'secondary'}
                          className="me-1"
                          onClick={() => edit(paiement._id)}
                        >
                          {' '}
                          Editer{' '}
                        </CButton>
                        <CButton
                          color={'danger'}
                          onClick={() => deleteFunction(paiement._id)}
                        >
                          {' '}
                          Supprimer{' '}
                        </CButton>
                      </CTableDataCell>
                    </CTableRow>
                  ))
                ) : (
                  <h3>{'NO Items'}</h3>
                )}
              </CTableBody>
            </CTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default Paiements
